/*------ Dark-styles ---------*/

@import "../scss/variables";

body.dark-mode {
  color: $dark-color;
  background-color: $dark-background !important;
}

body.dark-mode *::-webkit-scrollbar-thumb {
  background: $black-2;
}

.dark-mode {
  &.hover-submenu.sidenav-toggled.sidenav-toggled-open {
    .app-sidebar {
      .side-header .header-brand1 {
        .header-brand-img {
          &.desktop-logo,
          &.light-logo,
          &.light-logo1 {
            display: none !important;
          }

          &.toggle-logo {
            display: block !important;
          }
        }
      }

      .side-menu__icon {
        color: $white-7 !important;
      }

      .side-menu__item.active {
        .side-menu__icon {
          color: #fff !important;
        }
      }
    }
  }

  &.hover-submenu1.app.sidebar-mini.sidenav-toggled.sidenav-toggled-open
    .side-menu__icon {
    color: $dark-color;
  }
  &.hover-submenu1.sidebar-mini.sidenav-toggled .slide-menu {
    background: $dark-theme-color;
  }

  &.hover-submenu1.sidebar-mini.sidenav-toggled .side-menu-label1 a {
    color: $dark-color;
  }

  &.app.dark-mode.light-menu {
    .app-sidebar {
      .sub-slide-menu {
        .sub-slide-item,
        .sub-slide-item2,
        .sub-side-menu__item2 {
          &:before {
            color: $default-color !important;
          }
        }
      }

      .sub-slide-menu .sub-side-menu__item2:hover {
        .sub-side-menu__label2 {
          color: $primary !important;
        }
      }

      .sub-side-menu__item:hover {
        .sub-side-menu__label {
          color: $primary !important;
        }
      }

      .sub-side-menu__item:before {
        color: $default-color !important;
      }

      .side-menu .slide a:hover:before {
        color: $primary !important;
      }

      .side-menu .slide a:hover {
        color: $primary !important;

        &:before {
          color: $primary !important;
        }
      }

      .side-menu__item {
        &:hover,
        &:focus {
          .side-menu__label {
            color: $primary !important;
          }

          .side-menu__icon {
            color: $primary !important;
          }
        }
      }

      .slide-item:hover {
        &:before {
          color: $primary !important;
        }
      }

      .side-menu .slide a.active {
        color: $primary !important;

        &:before {
          color: $primary !important;
        }
      }

      .slide-menu li .slide-item:before {
        color: $default-color;
      }

      .side-menu .slide a {
        color: $default-color !important;
      }

      .side-menu__item.active {
        .side-menu__label {
          color: #fff !important;
        }

        i {
          color: #fff !important;
        }
      }

      .side-menu__icon {
        color: $default-color !important;
      }
    }
  }

  .table {
    color: $dark-color;
  }

  textarea:focus,
  input:focus {
    border-color: $dark-border !important;
  }

  .ms-search input {
    border: 1px solid $dark-border;
    background: $dark-theme-color
