  .selectgroup-button {
    border: 1px solid $dark-border;
    color: $white-7;
  }

  .selectgroup-input {
    &:checked + .selectgroup-button {
      background: $dark-background;
    }

    &:focus + .selectgroup-button {
      box-shadow: 0 0 0 2px rgba(98, 58, 162, 0.25);
    }
  }

  .custom-switch-indicator {
    background: $dark-border;
    border: 1px solid $dark-border;

    &:before {
      background: #fff;
      box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.4);
    }
  }

  .custom-switch-input:focus ~ .custom-switch-indicator {
    box-shadow: 0 0 0 2px rgba(98, 58, 162, 0.25);
    border-color: #7c46a8;
  }

  .custom-switch-description,
  .custom-switch-input:checked ~ .custom-switch-description {
    color: $white-7;
  }

  .imagecheck-figure {
    border: 1px solid $dark-border;
  }

  .imagecheck-input {
    &:focus ~ .imagecheck-figure {
      border-color: #467fcf;
      box-shadow: 0 0 0 2px rgba(98, 58, 162, 0.25);
    }

    &:checked ~ .imagecheck-figure {
      border-color: rgba(0, 40, 100, 0.24);
    }
  }

  .imagecheck-figure:before {
    background: #467fcf
      url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E")
      no-repeat center (center / 50%) 50%;
    color: #fff;
  }

  .imagecheck-caption,
  .imagecheck:hover .imagecheck-caption {
    color: $white-7;
  }

  .imagecheck-input {
    &:focus ~ .imagecheck-figure .imagecheck-caption,
    &:checked ~ .imagecheck-figure .imagecheck-caption {
      color: $white-7;
    }
  }

  .colorinput-color {
    border: 1px solid $dark-border;
    color: #fff;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  }

  .colorinput-input:focus ~ .colorinput-color {
    border-color: $dark-border;
    box-shadow: 0 0 0 2px rgba(98, 58, 162, 0.25);
  }

  .ui-datepicker {
    background-color: $dark-background;
    border: 1px solid $dark-border;

    .ui-datepicker-header {
      color: $dark-color;

      .ui-datepicker-next,
      .ui-datepicker-prev {
        text-indent: -99999px;
        color: #6c757d;
      }

      .ui-datepicker-next {
        &:hover::before,
        &:focus::before {
          color: $dark;
        }
      }

      .ui-datepicker-prev {
        &:hover::before,
        &:focus::before {
          color: $dark;
        }
      }

      .ui-datepicker-next-hover,
      .ui-datepicker-prev-hover {
        color: $white-7;
      }
    }

    .ui-datepicker-calendar {
      th {
        color: $white-7;
      }

      td {
        border: 1px solid $dark-border;
        background-color: $dark-border;

        span {
          background-color: $dark-background;
          color: $white-7;
        }

        a {
          background-color: $dark-background;
          color: $white-7;

          &:hover {
            background-color: $dark-theme-color;
            color: $dark-color;
          }
        }
      }

      .ui-datepicker-today a {
        background-color: $dark-theme-color;
        color: $dark-color;
      }
    }
  }

  .model-wrapper-demo {
    background: $dark-background;
  }

  .timeline__item:after {
    background: #fff !important;
  }

  .timeline__content {
    background-color: #fff;
  }

  #back-to-top {
    color: #fff;

    &:hover {
      background: $dark-theme-color !important;
    }
  }

  .timeline:before {
    background-color: $dark-border;
  }

  .timeline-item {
    &:first-child:before,
    &:last-child:before {
      background: #fff;
    }
  }

  .timeline-badge {
    border: 1px solid #fff;
    background: #adb5bd;
  }

  .timeline-time {
    color: $white-7;
  }

  .jvectormap-tip {
    background: #212529;
    color: white;
  }

  .jvectormap-zoomin,
  .jvectormap-zoomout,
  .jvectormap-goback {
    background: #45456c;
    color: #fff;
  }

  .selectize-control.plugin-drag_drop {
    &.multi > .selectize-input > div.ui-sortable-placeholder {
      background: #f2f2f2 !important;
      background: rgba(0, 0, 0, 0.06) !important;
      box-shadow: inset 0 0 12px 4px #fff;
    }

    .ui-sortable-helper {
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    }
  }

  .selectize-dropdown-header {
    border-block-end: 1px solid #d0d0d0;
    background: #f8f8f8;
  }

  .selectize-dropdown-header-close {
    color: $white-7;

    &:hover {
      color: #000;
    }
  }

  .selectize-dropdown.plugin-optgroup_columns .optgroup {
    border-inline-end: 1px solid #f2f2f2;
  }

  .selectize-control.plugin-remove_button {
    [data-value] {
      .remove {
        border-inline-start: 1px solid #d0d0d0;
      }

      &.active .remove {
        border-inline-start-color: #cacaca;
      }
    }

    .disabled [data-value] .remove {
      border-inline-start-color: #fff;
    }
  }

  .selectize-dropdown,
  .selectize-input {
    color: $white-7;
  }
}

