/*------ Navigation -------*/

.nav-sidebar .list-unstyled li:last-child {
  border-block-end: 0px;
}
.nav-item.show .dropdown-menu.dropdown-menu-arrow.show {
  inset-block-start: -1px !important;
}

.nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding-inline-start: 0;
  margin-block-end: 0;
  list-style: none;
}
.header-tabs .nav {
  flex-wrap: nowrap;
}
.nav-link {
  display: block;
  padding: 0.2rem 0.9rem;
  color: $primary;
  &:hover,
  &:focus {
    text-decoration: none;
    color: $primary;
  }
  &.disabled {
    color: #9ea7af;
  }
  &.disable {
    color: #cba4f7;
  }
}
.nav-tabs {
  border-block-end: 1px solid $border;
  .nav-item {
    margin-block-end: -1px;
    position: relative;
  }
  .nav-link {
    &:hover,
    &:focus,
    &.active {
      border: 0;
    }
  }
  .nav-item.show .nav-link {
    border: 0;
  }
  .dropdown-menu {
    border-start-start-radius: 0;
    border-start-end-radius: 0;
  }
}
.nav-pills {
  .nav-link.active,
  .show > .nav-link {
    color: $white;
  }
}
.nav-fill .nav-item {
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  text-align: center;
}
.nav-justified .nav-item {
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  text-align: center;
}
.nav.nav-pills.nav-stacked.labels-info p {
  color: #9d9f9e;
  font-size: 11px;
  margin-block-end: 0;
  padding: 0 22px;
}
.nav-item1 {
  padding: 10px;
  min-width: 2rem;
  transition: 0.3s color;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.nav1 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding-inline-start: 0;
  margin-block-end: 0;
  list-style: none;
  background: #f1f1f9;
}
.nav-item1 {
  &:hover:not(.disabled),
  &.active {
    color: $white;
  }
  .nav-link {
    &.disabled {
      opacity: 6;
      cursor: default;
      pointer-events: none;
    }
    &.disable {
      opacity: 3;
    }
  }
}
.nav-tabs {
  border-block-end: 1px solid $border;
  .nav-item1 {
    margin-block-end: -1px;
    position: relative;
    &.nav-link {
      border: 1px solid transparent;
      border-start-start-radius: 3px;
      border-start-end-radius: 3px;
    }
    .nav-link {
      border: 0;
      color: inherit;
      margin-block-end: -1px;
      color: $muted;
      transition: 0.3s border-color;
      font-weight: 400;
      &:hover:not(.disabled),
      &.active {
        color: $white;
      }
      &.disabled {
        opacity: 0.6;
        cursor: default;
        pointer-events: none;
      }
    }
    i {
      margin-inline-end: 0.25rem;
      line-height: 1;
      font-size: 0rem;
      width: 0.875rem;
      vertical-align: baseline;
      display: inline-block;
      margin-inline-end: 10px;
    }
    &:hover .nav-submenu {
      display: block;
    }
  }
  .nav-submenu .nav-item1 {
    display: block;
    padding: 0.5rem 1rem;
    color: #9aa0ac;
    margin: 0 !important;
    cursor: pointer;
    transition: 0.3s background;
    &.active {
      color: #467fcf;
    }
    &:hover {
      color: #6e7687;
      text-decoration: none;
      background: rgba(0, 0, 0, 0.024);
    }
  }
}
.nav-unread {
  position: absolute;
  inset-block-start: 0.4rem;
  background: #ecd938;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  &.badge {
    position: absolute;
    inset-block-start: 4px;
    inset-inline-start: 26px;
    display: block !important;
    padding: 3px 5px !important;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    font-size: 11px;
  }
}
.nav-link {
  &.icon {
    margin: 5px 0px;
    padding: 12px;
    text-align: center;
    height: 2.5rem;
    font-size: 1.2rem;
    position: relative;
    i {
      color: $muted !important;
    }
  }
  min-width: 2rem;
  transition: 0.3s color;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
@media (max-width: 768px) {
  .nav-link.icon {
    padding: 5px !important;
  }
}
.nav-item {
  min-width: 2rem;
  transition: 0.3s color;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.header-tabs .nav-item{
	min-width: none;

}
.nav-tabs {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: $black;
  margin: 0 -0.75rem;
  .nav-link {
    border: 0;
    color: inherit;
    color: $muted;
    transition: 0.3s border-color;
    font-weight: 500;
    padding: 1rem 1.8rem;
    font-size: 13px;
    text-align: center;
    &:hover:not(.disabled),
    &.active {
      color: $white;
    }
    &.disabled {
      opacity: 0.4;
      cursor: default;
      pointer-events: none;
      color: #868e96;
      background-color: transparent;
      border-color: transparent;
    }
  }
  .nav-item:hover .nav-submenu {
    display: block;
  }
}
.nav-link:hover .dropdown-menu,
.nav-item:hover .dropdown-menu,
.nav-link:hover .dropdown-menu.show {
  display: block;
}
@media (max-width: 1279px) and (min-width: 992px) {
  .nav-tabs .nav-link {
    padding: 1rem 0.95rem;
    font-size: 13px;
  }
}
.nav-tabs {
  .nav-item i {
    margin-inline-end: 0.25rem;
    line-height: 1;
    font-size: 0rem;
    width: 0.875rem;
    vertical-align: baseline;
    display: inline-block;
    margin-inline-end: 10px;
  }
  .nav-submenu {
    display: none;
    position: absolute;
    background: $white;
    border: 1px solid $border;
    border-block-start: none;
    z-index: 10;
    box-shadow: 0 1px 2px 0 $black-05;
    min-width: 10rem;
    border-radius: 0 0 3px 3px;
    .nav-item {
      display: block;
      padding: 0.5rem 1rem;
      color: #9aa0ac;
      margin: 0 !important;
      cursor: pointer;
      transition: 0.3s background;
      &.active {
        color: #467fcf;
      }
      &:hover {
        color: #6e7687;
        text-decoration: none;
        background: rgba(0, 0, 0, 0.024);
      }
    }
  }
}
.hor-menu .nav-badge {
  border-radius: 30px;
  position: absolute;
  inset-block-start: 3px;
  inset-inline-end: 14px;
  padding: 0.2em 0.4em;
  font-size: 12px;
}
@media (max-width: 992px) {
  .hor-menu .nav-badge {
    inset-inline-end: auto !important;
  }
}
#smartwizard {
  .nav.nav-tabs.step-anchor {
    border-block-end: 0 !important;
    padding: 30px;
  }
  .nav-tabs {
    border-block-start: 0 !important;
  }
  .nav-link {
    padding: 8px 40px;
    border-radius: 5px !important;
  }
}
.sw-theme-dots .nav-tabs .nav-link:hover:not(.disabled) {
  background: transparent;
}
#basicwizard .nav-tabs .nav-link {
  padding: 0.8rem 0.8rem;
}
