/*-----Custom-styles-----*/
.fade {
  transition: opacity 0.15s linear;

  &:not(.show) {
    opacity: 0;
  }
}

@media screen and (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}

.collapse:not(.show) {
  display: none;
  background: $white;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}

@media screen and (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}

.notifyimg {
  color: $white;
  float: $float-start;
  height: 40px;
  line-height: 40px;
  margin-inline-end: 10px;
  text-align: center;
  vertical-align: middle;
  width: 40px;
}

.block {
  display: block !important;
}

.user-semi-title {
  font-size: 14px;
  line-height: 23px;
}

.notifications-menu {
  overflow-y: auto;
  max-height: 300px;
}

.message-menu {
  overflow-y: auto;
  max-height: 300px;
}

/*----- Global Loader -----*/

#global-loader {
  position: fixed;
  z-index: 50000;
  background: $white;
  inset-inline-start: 0;
  inset-block-start: 0;
  inset-inline-end: 0;
  inset-block-end: 0;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

ul {
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}

#popup {
  position: absolute;
  width: auto;
  height: 30px;
  background: #fe6b1f;
  display: none;
  color: $white;
  border-radius: 5px;
}

#copy {
  background: none;
  color: $white;
  font-weight: bold;
  padding: 8px 25px;
  border: 0;
}

.d-flex {
  display: flex !important;
}

.popsecondary {
  color: $secondary;
}

.popinfo {
  color: $info;
}

.popsuccess {
  color: $success;
}

.popdanger {
  color: $danger;
}

.popwarning {
  color: $warning;
}

.more-less {
  float: $float-end;
  color: #212121;
}

.wrap {
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px $black-2,
    0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
}

a {
  &:focus,
  &:hover,
  &:active {
    outline: 0;
    text-decoration: none;
  }
}

@-webkit-keyframes progress-bar-stripes {
  from {
    background-position: 1rem 0;
  }

  to {
    background-position: 0 0;
  }
}

@keyframes progress-bar-stripes {
  from {
    background-position: 1rem 0;
  }

  to {
    background-position: 0 0;
  }
}

.content-heading {
  font-weight: 400;
  margin: 2rem 0 1.5rem;
  font-size: 1.25rem;
  line-height: 1.25;

  &:first-child {
    margin-block-start: 0;
  }
}

.aside {
  position: fixed;
  inset-block-start: 0;
  inset-inline-end: 0;
  inset-block-end: 0;
  width: 22rem;
  background: $white;
  border-inline-start: 1px solid $border;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  z-index: 100;
  visibility: hidden;
  box-shadow: 0 0 5px 2px $black-05;
}

@media (min-width: 1600px) {
  body.aside-opened .aside {
    visibility: visible;
  }
}

.aside-body {
  padding: 1.5rem;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  overflow: auto;
}

.center-block {
  margin-inline-end: auto;
  margin-inline-start: auto;
  float: inherit !important;
}

.tracking-tight {
  letter-spacing: -0.05em !important;
}

.tracking-normal {
  letter-spacing: 0 !important;
}

.tracking-wide {
  letter-spacing: 0.05em !important;
}

.leading-none {
  line-height: 1 !important;
}

.leading-tight {
  line-height: 1.25 !important;
}

.leading-normal {
  line-height: 1.5 !important;
}

.leading-loose {
  line-height: 3 !important;
}

.icon i {
  vertical-align: -1px;
}

a.icon {
  text-decoration: none;
  cursor: pointer;

  &:hover {
    color: #1a1a1a !important;
  }
}

.o-auto {
  overflow: auto !important;
}

.o-hidden {
  overflow: hidden !important;
}

.shadow {
  box-shadow: 0 1px 2px 0 $black-05 !important;
}

