.material-switch > {
  input[type="checkbox"] {
    display: none;
  }

  label {
    cursor: pointer;
    height: 0px;
    position: relative;
    width: 40px;

    &::before {
      background: $muted;
      box-shadow: inset 0px 0px 10px rgba(228, 229, 237, 0.5);
      border-radius: 8px;
      content: "";
      height: 16px;
      margin-block-start: -8px;
      position: absolute;
      opacity: 0.3;
      transition: all 0.4s ease-in-out;
      width: 40px;
    }

    &::after {
      background: $white;
      border-radius: 16px;
      box-shadow: 0px 0px 5px rgba(228, 229, 237, 0.9);
      content: "";
      height: 24px;
      inset-inline-start: -4px;
      margin-block-start: -8px;
      position: absolute;
      inset-block-start: -4px;
      transition: all 0.3s ease-in-out;
      width: 24px;
    }
  }

  input[type="checkbox"]:checked + label {
    &::before {
      background: inherit;
      opacity: 0.5;
    }

    &::after {
      background: inherit;
      inset-inline-start: 20px;
    }
  }
}

