/*--- WIZARD ELEMENTS ---*/

.wizard {
  border: 1px solid $border;
  background-color: $white;

  > {
    .steps {
      padding: 20px;

      > ul {
        padding: 0;
        margin-block-end: 0;
        display: flex;

        li {
          float: none;
          display: block;
          width: auto;

          .current-info {
            display: none;
          }

          .title {
            margin-inline-start: 5px;
            white-space: nowrap;
            transition: all 0.2s ease-in-out;
            margin: 0;
            min-height: auto;
          }

          + li {
            margin-inline-start: 5px;
          }
        }
      }

      a {
        color: $primary-01;
        font-weight: 500;
        font-size: 15px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover,
        &:active {
          color: $primary-01;
          font-weight: 500;
          font-size: 15px;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .number,
        &:hover .number,
        &:active .number {
          flex-shrink: 0;
          font-weight: 700;
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            "Helvetica Neue", Arial, "Noto Sans", sans-serif,
            "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
            "Noto Color Emoji";
          display: block;
          text-align: center;
          line-height: 2;
          width: 30px;
          height: 30px;
          background-color: $primary-01;
          border-radius: 100%;
          transition: all 0.2s ease-in-out;
          margin-inline-end: 5px;
        }
      }

      .disabled a {
        color: $primary-06;

        &:hover,
        &:active {
          color: $primary-06;
        }
      }

      .current a {
        color: $primary-01;

        &:hover,
        &:active {
          color: $primary-01;
        }

        .title,
        &:hover .title,
        &:active .title {
          display: inline-block;
        }

        .number,
        &:hover .number,
        &:active .number {
          background-color: $primary-01;
          color: $white;
        }
      }

      .done a {
        color: $success;

        &:hover,
        &:active {
          color: $success;
        }

        .title,
        &:hover .title,
        &:active .title {
          display: none;
        }

        .number,
        &:hover .number,
        &:active .number {
          background-color: $success;
          color: $white;
        }
      }
    }

    .content {
      border-block-start: 1px solid $border;
      border-block-end: 1px solid $border;
      min-height: 150px;
      padding: 20px;

      > {
        .title {
          font-size: 18px;
          color: $default-color;
          font-weight: 600;
          padding-block-end: 15px;
          display: none;
          margin: 0;

          &.current {
            display: block;
          }
        }

        .body {
          float: none;
          position: static;
          width: auto;
          height: auto;

          input.parsley-error {
            border-color: #ff5c77;

            + ul {
              list-style: none !important;
            }
          }
        }
      }
    }

    .actions {
      padding: 20px;

      > ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;

        > li:last-child a {
          background-color: $success;
        }
      }

      a {
        display: block;
        background-color: $primary;
        padding: 9px 25px;
        line-height: 1.573;
        color: $white;
        border-radius: 4px;
        font-weight: 500;

        &:hover,
        &:active {
          display: block;
          background-color: $primary-09;
          padding: 9px 25px;
          line-height: 1.573;
          color: $white;
          border-radius: 4px;
          font-weight: 500;
        }
      }

      .disabled a {
        background-color: #e8ebf2;
        color: $default-color;

        &:hover,
        &:active {
          background-color: #e8ebf2;
          color: $default-color;
        }
      }
    }
  }

  &.vertical > {
    .steps {
      padding: 20px;

      ul li {
        + li {
          margin-inline-start: 10px;
        }

        .title {
          display: none;
        }
      }

      .current a .title {
        display: inline-block;
      }
    }

    .content {
      margin: 0;
      padding: 20px;
    }

    .actions {
      padding: 20px;

      ul {
        float: none;
        margin: 0;
        padding: 0;
      }
    }
  }
}

