/***** CUSTOM STYLES *****/

.wizard-style-1 > .steps > ul {
  a {
    padding: 0;
    height: 50px;

    &:hover,
    &:active {
      padding: 0;
      height: 50px;
    }

    .number,
    &:hover .number,
    &:active .number {
      width: 50px;
      height: 100%;
      border: 0;
      font-size: 18px;
      font-weight: bold;
      color: $white;
      background-color: $primary !important;
      border-radius: 0;
    }

    .title,
    &:hover .title,
    &:active .title {
      margin-inline-end: 20px;
      margin-inline-start: 20px;
    }
  }

  .current a {
    .number,
    &:hover .number,
    &:active .number {
      color: $white;
    }
  }

  .done a {
    .number,
    &:hover .number,
    &:active .number {
      background-color: #643ab0;
      color: $white;
    }
  }
}

.wizard-style-2 > .steps > ul {
  a {
    padding: 0;
    height: 50px;
    border-radius: 50px;

    &:hover,
    &:active {
      padding: 0;
      height: 50px;
      border-radius: 50px;
    }

    .number,
    &:hover .number,
    &:active .number {
      width: 50px;
      height: 100%;
      border: 2px solid #f3f7fd;
      font-size: 18px;
      font-weight: bold;
      color: #737688;
      background-color: $white;
    }

    .title,
    &:hover .title,
    &:active .title {
      margin-inline-end: 20px;
    }
  }

  .current a {
    .number,
    &:hover .number,
    &:active .number {
      color: $primary-01;
    }
  }

  .done a {
    .number,
    &:hover .number,
    &:active .number {
      border-color: #8c3feb;
      color: #8c3feb;
    }
  }
}

